<template lang="html">
  <div class="yggl">
    <div class="yggl-wrapper">
      <div class="yggl-wrapper-top">
        <div>
          <span>运费账户</span>
          <span>/</span>
          <span>说明说明说明说明说明说明说明说明</span>
				</div>
				<div>EXCEL导出</div>
      </div>
      <div class="yggl-wrapper-bottom">
        <div class="con">
          <div class="addGL">
            <el-button type="primary">添加管理员</el-button>
            <p>点击后显示二维码 微信扫描加入</p>
          </div>
          <div class="table">
            <el-table :data="tableData4" border style="width: 100%" max-height="250">
              <el-table-column>
                <template scope="scope">
                  <img src="../../../assets/u581_03.png" alt="">
                </template>
              </el-table-column>
              <el-table-column prop="gly" label="管理员"></el-table-column>
              <el-table-column prop="tele" label="手机号"></el-table-column>
              <el-table-column prop="data" label="加入时间"></el-table-column>
              <el-table-column prop="qxjs" label="权限角色"></el-table-column>
              <el-table-column label="操作" width="120">
                <template scope="scope">
                  <el-button type="text" size="small">权限</el-button>
                  |
                  <el-button @click.native.prevent="deleteRow(scope.$index, tableData4)" type="text" size="small" style="color: #DD9B93;">
                    移除
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <div class="addGL">
            <el-button type="primary">新增权限角色</el-button>
          </div>
          <div class="table">
            <el-table :data="tableData5" border style="width: 100%" max-height="250">
              <el-table-column prop="qxms" label="权限描述"></el-table-column>
              <el-table-column prop="jsmc" label="角色名称"></el-table-column>
              <el-table-column prop="yhcz" label="用户列表"></el-table-column>
              <el-table-column label="操作">
                <template scope="scope">
                  <el-button type="text" size="small">权限</el-button>
                  |
                  <el-button type="text" size="small">权限设置</el-button>
                  |
                  <el-button @click.native.prevent="deleteRow2(scope.$index, tableData5)" type="text" size="small" style="color: #DD9B93;">
                    移除
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>

      </div>
    </div>

  </div>
</template>

<script>
export default {
  methods: {
      deleteRow(index, rows) {
        rows.splice(index, 1);
      },
      deleteRow2(index, rows) {
        rows.splice(index, 1);
      }
    },
    data() {
      return {
        tableData4: [{
          gly: '欧阳锋',
          tele: '13829893987',
          data: '2016-11-20 16.30.35',
          qxjs: '超级管理员'
        },
        {
          gly: '黄蓉',
          tele: '13809893987',
          data: '2016-11-20 16.30.35',
          qxjs: '领导'
        }
      ],
      tableData5: [{
        qxms: '超级管理员',
        jsmc: '享有全部权限',
        yhlb: '黄蓉、郭靖',
      },
      {
        qxms: '领导',
        jsmc: '享有全部权限',
        yhlb: '',
      },
      {
        qxms: '保险专员',
        jsmc: '专门管理保险相关事务',
        yhlb: '',
      }

    ]
    }
  }
}
</script>

<style lang="less">
.yggl{
  width: 1413px;
  background-color: #FAFBFF;
  border-left:  1px solid #EAEAF2;
  .yggl-wrapper{
    width: 1314px;
    margin: 0 auto;
    .yggl-wrapper-top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 90px;
      line-height: 90px;
      div:nth-of-type(1){
        span:nth-of-type(1){
          font-size: 20px;
          color: #3257a7;
          font-weight: 700;
        }
        span:nth-of-type(2){
          margin: 0 5px;
        }
        span{
          font-size: 16px;
          color: #9ba5be;
        }
      }
      div:nth-of-type(2){
        width: 100px;
				height: 40px;
				color: #417640;
				border: 1px solid #417640;
				line-height: 40px;
				text-align: center;
				float: left;
				border-radius: 5px;
				margin-left: 10px;
      }
    }
    .yggl-wrapper-bottom{
      border: 1px solid #EAEAF2;
      background-color: #fff;
      padding-bottom: 300px;
      .con{
        width: 1280px;
        margin:0 auto;
        .addGL{
          margin-top: 32px;
          margin-bottom: 20px;
          display: flex;
          align-items: center;
          font-size: 12px;
          color: #d9dee4;
          p{
            margin-left: 10px;
          }
        }
      }
    }
  }
}
</style>



